"use client";

import { useState, useRef } from 'react';
// import { FFmpeg } from '@ffmpeg/ffmpeg';

// Custom implementation to avoid "expression is too dynamic" bundler error with @ffmpeg/util
const toBlobURL = async (url: string, mimeType: string) => {
    const resp = await fetch(url);
    if (!resp.ok) throw new Error(`Failed to fetch ${url}`);
    const blob = await resp.blob();
    return URL.createObjectURL(new Blob([blob], { type: mimeType }));
};

const fetchFile = async (source: File | string) => {
    if (typeof source === 'string') {
        const resp = await fetch(source);
        if (!resp.ok) throw new Error(`Failed to fetch ${source}`);
        return new Uint8Array(await resp.arrayBuffer());
    } else {
        return new Uint8Array(await source.arrayBuffer());
    }
};

export const useFFmpeg = () => {
    const [loaded, setLoaded] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const ffmpegRef = useRef<any>(null);

    const load = async () => {
        if (loaded) return;
        setIsLoading(true);
        const baseURL = '/ffmpeg';

        try {
            // Load from global scope injected by script
            if (!ffmpegRef.current) {
                // @ts-ignore
                const FFmpegClass = window.FFmpegWASM?.FFmpeg || window.FFmpeg?.FFmpeg;

                if (!FFmpegClass) {
                    throw new Error("FFmpeg library not loaded yet. Component waiting for Script.");
                }

                ffmpegRef.current = new FFmpegClass();
            }

            const ffmpeg = ffmpegRef.current;

            // Log messages
            ffmpeg.on('log', ({ message }: { message: string }) => {
                console.log(message);
            });

            await ffmpeg.load({
                coreURL: `${baseURL}/ffmpeg-core.js`,
                wasmURL: `${baseURL}/ffmpeg-core.wasm`,
                workerURL: `${baseURL}/ffmpeg-core.worker.js`,
            });
            setLoaded(true);
        } catch (e) {
            console.error("FFmpeg load failed", e);
        } finally {
            setIsLoading(false);
        }
    };

    const extractSubtitles = async (file: File): Promise<string | null> => {
        if (!loaded || !ffmpegRef.current) return null;
        const ffmpeg = ffmpegRef.current;

        try {
            // Mount file
            const data = await fetchFile(file);
            await ffmpeg.writeFile('input.mkv', data);

            // Check for subtitles
            // Command: ffmpeg -i input.mkv -map 0:s:0 -f webvtt output.vtt
            // -y to overwrite output if exists
            await ffmpeg.exec(['-i', 'input.mkv', '-map', '0:s:0', '-f', 'webvtt', 'output.vtt', '-y']);

            const outputData = await ffmpeg.readFile('output.vtt');

            // Cleanup
            await ffmpeg.deleteFile('input.mkv');
            await ffmpeg.deleteFile('output.vtt');

            // Convert to blob
            // outputData is Uint8Array or string, cast safely
            const blob = new Blob([outputData as any], { type: 'text/vtt' });
            return URL.createObjectURL(blob);
        } catch (e) {
            console.error("Extraction failed or no subtitles", e);
            try { await ffmpeg.deleteFile('input.mkv'); } catch { }
            return null;
        }
    };

    return { loaded, isLoading, load, extractSubtitles };
}
